html{font-size:calc(1em + 1vw)}body{width:100%;max-width:1400px;margin:auto;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}input{width:100%}input,button,.timeContainer{background-color:transparent;border:1px dotted #fff;color:#fff;margin:0.5rem;font-size:0.7rem}input:hover,button:hover,.timeContainer:hover{opacity:0.5}input>span,button>span,.timeContainer>span{margin:0.2rem}ul{margin:0;padding:0}ul li{display:inline}ul li:not(:first-child){margin-left:0.5rem}.wrapper{display:grid;grid-gap:5px;margin:1em}.input_wrapper{display:grid;grid-template-columns:1fr 1fr;margin-bottom:1em;grid-gap:10px}.menu_wrapper{display:flex;background-color:black;opacity:0.8;border-radius:2%}.search_container{display:grid;grid-template-columns:1fr}@media (max-width: 768px){.input_wrapper{display:grid;grid-template-columns:1fr;margin-bottom:1em;grid-gap:10px}}.info_card{border-radius:0.5rem;box-shadow:0 3px 5px rgba(0,0,0,0.16),0 3px 5px rgba(0,0,0,0.23);padding:2rem 2rem 0.5rem;text-align:center;opacity:1;z-index:100}.sunny{background-image:linear-gradient(0deg, #b3e5fc, #29b6f6);opacity:1}.weather{display:grid;grid-template-columns:1fr 1fr;grid-gap:5px;opacity:0.8}.temperature{display:flex;flex-flow:column;justify-content:center;align-items:center;background-color:aliceblue;border-radius:2%}.temperature_temp{font-size:4em}.temperature_place{font-size:1em}.weatherDetails{display:flex;flex-flow:column;justify-content:flex-start;background-color:cadetblue;border-radius:2%}.weatherForeCast{display:grid;grid-template-columns:repeat(auto-fit, minmax(250px, 1fr));opacity:0.8;border-radius:2%;grid-gap:5px;margin:5px}.map{background:aliceblue;text-align:center;width:100%;height:75vh}.mdi-icon{color:aliceblue}.imgContainer{width:5em;height:auto}.temperature_container{display:flex;align-items:center;vertical-align:center}@media (max-width: 768px){.info_card{border-radius:0.5rem;box-shadow:0 3px 5px rgba(0,0,0,0.16),0 3px 5px rgba(0,0,0,0.23);padding:0.1rem 0.1rem 0.1rem;text-align:center;opacity:1;z-index:100}.weather{display:grid;grid-template-columns:1fr;grid-gap:5px;opacity:0.8}}.selected_unit{color:bisque}.voice_search{color:white;display:flex;align-items:center;font-size:0.7em;margin:0.5em}.forecastDetails{display:flex;flex-flow:column;align-items:center;background-color:darkslategrey;border-radius:2%}.mic_on{color:transparent;text-shadow:0 0 0 red}.mic_off{color:transparent;text-shadow:0 0 0 white}.play{color:white;font-size:1.0rem}

